/*---------------------------------------------------------
 * mnuiux/src/scss/style/_content.scss
 *---------------------------------------------------------
 */

    #content {
        @extend %fixed-extension;

        top: 10vh;
        left: 0px;
        width: 100vw;
        height: 80vh;

        background: rgba(255, 255, 255, 0.75);

        overflow-x: hidden;
        overflow-y: auto;

        z-index: 100;
    }

        .content-page {
            @extend %base-extension;

            margin: 0;
            padding: 2.5vh 2.5vh 2.5vh 1vh;

            width: 100%;
            height: auto;

            color: #146;
            background: rgba(0, 0, 0, 0);

            font-family: $text-font;

            overflow-x: hidden;
            overflow-y: hidden;
        }

        .page-nav,
        .text-nav
        {
            margin: 0;
            padding: 0;
            color: #368;
            display: inline;
            text-decoration: none;
            font-weight: bold;
            cursor: pointer;
        }

            .page-nav:hover,
            .text-nav:hover
            {
                text-decoration: underline;
            }

        .thumbnail-left {
            display: inline-block;
            width: 100%;
            height: auto;
            
            padding: 1vh 2.75vh 2.75vh 1vh;
        }

        .thumbnail-right {
            display: inline-block;
            width: 100%;
            height: auto;
            
            padding: 0vh 2.75vh 2.75vh 2.75vh;
        }

        .embed-right {
            display: inline-block;
            width: 100%;
            height: 61vw;

            padding: 0vh 0vh 2.75vh 0vh;
        }

        .content-page h1 {
            box-sizing: border-box;
            display: inline-block;
            font-family: $header-font;
            font-size: 1.5em;
            padding-top: 0;
        }

        .content-page h3 {
            box-sizing: border-box;
            display: inline-block;
            font-family: $header-font;
        }

        .content-page p {
            margin: 0;
            padding: 0;
            padding-right: 1vw;
            margin-bottom: 2.75vh;
        }

            .content-page:last-child {
                margin-bottom: 0;
            }

        .content-spacer {
            width: 100%;
            height: 0;
            background: #000;
        }

        pre {
            color: #FAFAFA;
            background: #474949;
        }
        