 */

    #small-navigation {
        position: absolute;

        display: none;
        opacity: 0.01;

        top: 2.75vh;
        right: 1vh;
        width: calc(13.5vh + 6px);
        height: 4.5vh;

        cursor: pointer;
    }

        .small-inner {
            position: absolute;

            margin-right: 2px;

            width: 4.5vh;
            height: 4.5vh;

            background-size: calc(100% - 2px) calc(100% - 2px);
            background-position: 0px 0px;
            background-repeat: no-repeat;
        }

        .small-image {
            @extend %base-extension;

            margin-top: 2px;
            margin-left: 2px;
            width: calc(100% - 2px);
            height: calc(100% -2px);
        }

            #small-Home {
                background-image: url(/images/Icons/Tile-Home-shadow.png);
            }

            #small-Blog {
                background-image: url(/images/Icons/Tile-Blog-shadow.png);
            }

            #small-Projects {
                background-image: url(/images/Icons/Tile-Projects-shadow.png);
            }

            #small-Contact {
                background-image: url(/images/Icons/Tile-Contact-shadow.png);
            }
