    header {
        @extend %fixed-extension;

        top: 0vh;
        left: 0vh;
        width: 100vw;

        color: #FFF;
        background: #899;

        z-index: 200;
    }

    #header-padding {
        @extend %fixed-extension;

        top: 0vh;
        left: 0vw;
        width: 100vw;
        height: 10vh;
        
        background: rgba(255, 255, 255, 0.75);
    }

