/*---------------------------------------------------------
 * mnuiux/src/scss/config/_extensions.scss
 *---------------------------------------------------------
 */

    %base-extension {
        position: relative;
        float: left;

        box-sizing: border-box;
    }

    %fixed-extension {
        position: fixed;

        box-sizing: border-box;
    }
    
    %noselect-extension {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
      }
    