/*---------------------------------------------------------
 * mnuiux/src/scss/post/_responsive.scss
 *---------------------------------------------------------
 */

    @media only screen and (min-width: #{$tablet-min})
    {
        #header,
        #content,
        #footer
        {
            padding: 0vh #{$tablet-pad};
        }

        #title-logo {
            left: #{$tablet-pad};
        }

        #small-navigation {
            right: #{$tablet-pad};
        }

        .blog-preview img {
            width: 30%;
            height: auto;
        }

        .blog-preview p {
            width: 70%;
            height: auto;
            margin-top: -1vh;
            padding: 0 2.5vh;
        }
    }

    @media only screen and (min-width: #{$desktop-min})
    {
        #header,
        #content,
        #footer
        {
            padding: 0vh #{$desktop-pad};
        }

        #title-logo {
            left: #{$desktop-pad};
        }

        #small-navigation {
            right: #{$desktop-pad};
        }

        .thumbnail-left,
        .thumbnail-right
        {
            padding-left: 0;
            width: 40%;
        }

        .content-page {
            padding-left: 0;
        }

        .content-page h1 {
            font-size: 1.5em;
            margin-top: 1vh;
        }

        .embed-right {
            width: 40vw;
            height: 22vw; 
            padding: 0vh 1vh 2.75vh 1vh
        }

        .blog-preview img {
            width: 25%;
        }

        .blog-preview p {
            width: 75%;
            font-size: 1.25em;
        }
    }

    @media only screen and (min-width: #{$widescreen-min})
    {
        #header,
        #content,
        #footer
        {
            padding: 0vh #{$widescreen-pad};
        }

        #title-logo {
            left: #{$widescreen-pad};
        }

        #small-navigation {
            right: #{$widescreen-pad};
        }
    }

    @media only screen and (min-width: #{$humungous-min})
    {
        #header,
        #content,
        #footer
        {
            padding: 0vh #{$humungous-pad};
        }

        #title-logo {
            left: #{$humungous-pad};
        }

        #small-navigation {
            right: #{$humungous-pad};
        }
    }
