/*---------------------------------------------------------
 * mnuiux/src/scss/style/_background.scss
 *---------------------------------------------------------
 */

    .color-change {
        animation: changeColors 300s infinite;
    }


    @keyframes changeColors {
        0% {
            background: #999;
        }
        12% {
            background: #99A;
        }
        24% {
            background: #9A9;
        }
        36% {
            background: #9AA;
        }
        48% {
            background: #A99;
        }
        60% {
            background: #A9A;
        }
        72% {
            background: #AA9;
        }
        84% {
            background: #AAA;
        }
        96% {
            background: #9A9A9A;
        }
        100% {
            background: #999;
        }
    }


