/*---------------------------------------------------------
 * mnuiux/src/scss/style/_blog.scss
 *---------------------------------------------------------
 */

    .blog-preview {
        @extend %base-extension;

        margin-left: 2.5vh;
        margin-bottom: 5vh;
        padding: 1vh;

        width: calc(100% - 5vh);
        height: auto;

        background: rgba(255, 255, 255, 0.5);
        box-shadow: -4px 4px 8px rgba(0, 0, 0, 0.25);
    }

        .blog-preview a {
            @extend %base-extension;

            width: 100%;
            height: 7.5vh;

            font-size: 5vh;
            font-weight: bold;
            font-family: $link-font;

            text-decoration: none;
        }

        .blog-preview img {
            @extend %base-extension;

            width: 100%;
            height: auto;
        }

        .blog-preview p {
            @extend %base-extension;
            float: right;

            margin-top: 2.5vh;
            width: 100%;
            height: auto;

            font-size: 1em;
            font-family: $text-font;
        }
