 *---------------------------------------------------------
 */

    #nav {
        position: absolute;

        display: none;
        opacity: 0.01;

        margin: 0;
        padding: 0;

        top: 74vh;
        left: calc((100% - 24vh) / 2);
        width: 24vh;
        height: 6vh;
    }

    .navigation-link {
        position: absolute;

        transform-style: preserve-3d;

        width: 6vh;
        height: 6vh;

        background-size: calc(100% - 4px) calc(100% - 4px);
        background-position: 0px 0px;
        background-repeat: no-repeat;
    }

    .nav-image {
        margin-top: 4px;
        margin-left: 4px;
        width: calc(100% - 4px);
        height: calc(100% - 4px);
    }

    #navigation-Home {
        top: 0vh;
        left: 0vh;
        background-image: url(/images/Icons/Tile-Home-shadow.png);
    }

    #navigation-Projects {
        top: 0vh;
        left: 6vh;
        background-image: url(/images/Icons/Tile-Projects-shadow.png);
    }

    #navigation-Blog {
        top: 0vh;
        left: 12vh;
        background-image: url(/images/Icons/Tile-Blog-shadow.png);
    }

    #navigation-Contact {
        top: 0vh;
        left: 18vh;
        background-image: url(/images/Icons/Tile-Contact-shadow.png);
    }

