/*---------------------------------------------------------
 * mnuiux/src/scss/style/_title.scss
 *---------------------------------------------------------
 */

    #title-logo {
        position: absolute;

        display: none;
        opacity: 0.01;

        top: 2.75vh;
        left: 1vh;
        width: auto;
        height: 4.5vh;

        cursor: pointer;
    }

        .title-inner {
            @extend %base-extension;

            margin-right: 2px;

            width: 4.5vh;
            height: 4.5vh;

            background-size: calc(100% - 2px) calc(100% - 2px);
            background-position: 0px 0px;
            background-repeat: no-repeat;
        }

        .title-tile {
            @extend %base-extension;

            margin-top: 2px;
            margin-left: 2px;
            width: calc(100% - 2px);
            height: calc(100% -2px);
        }

            #title-shadow-M {
                background-image: url(/images/Icons/Small-M-shadow.png);
            }

            #title-shadow-N {
                background-image: url(/images/Icons/Small-N-shadow.png);
            }

            #title-shadow-U1 {
                background-image: url(/images/Icons/Small-U1-shadow.png);
            }

            #title-shadow-I {
                background-image: url(/images/Icons/Small-I-shadow.png);
            }

            #title-shadow-U2 {
                background-image: url(/images/Icons/Small-U2-shadow.png);
            }

            #title-shadow-X {
                background-image: url(/images/Icons/Small-X-shadow.png);
            }
