/*---------------------------------------------------------
 * mnuiux/src/scss/style/_layout.scss
 *---------------------------------------------------------
 */

    body
    {
        margin: 0;
        padding: 0;

        width: 100vw;
        height: auto;

        box-sizing: border-box;

        color: #FFF;
        background: #899;

        overflow-x: hidden;
        overflow-y: hidden;
    }


    #outer {
        @extend %base-extension;

        width: 100%;
        height: auto;

        min-height: 100%;
    }

        #inner {
            @extend %base-extension;

            width: 100%;
            height: auto;

            min-height: 100%;
        }

