    footer {
        @extend %fixed-extension;

        left: 0vh;
        width: 100vw;
        bottom: 0px;

        color: #FFF;
        background: #899;

        z-index: 300;
    }

    #footer-padding {
        @extend %fixed-extension;

        top: 90vh;
        left: 0vw;
        width: 100vw;
        height: 10vh;
        
        background: rgba(255, 255, 255, 0.75);
    }

