/*---------------------------------------------------------
 * mnuiux/src/scss/style/_logo.scss
 *---------------------------------------------------------
 */

    #logo {
        @extend %fixed-extension;

        display: none;
        opacity: 0.01;

        top: 19vh;
        left: calc((100% - 24vh) / 2);
        width: 24vh;
        height: 36vh;

        overflow: hidden;

        z-index: 500;
    }

        .logo-inner {
            position: absolute;

            top: 12vh;
            left: 6vh;
            width: 12vh;
            height: 12vh;

            background-size: calc(100% - 6px) calc(100% - 6px);
            background-position: 0px 0px;
            background-repeat: no-repeat;
        }

        .logo-image {
            margin-top: 6px;
            margin-left: 6px;
            width: calc(100% - 6px);
            height: calc(100% -6px);
        }

        #logo-inner-0 {
            top: 0vh;
            left: 0vh;
            background-image: url(/images/Icons/Tile-M-shadow.png);
        }

        #logo-inner-1 {
            top: 0vh;
            left: 12vh;
            background-image: url(/images/Icons/Tile-N-shadow.png);
        }

        #logo-inner-2 {
            top: 12vh;
            left: 0vh;
            background-image: url(/images/Icons/Tile-U1-shadow.png);
        }

        #logo-inner-3 {
            top: 12vh;
            left: 12vh;
            background-image: url(/images/Icons/Tile-I-shadow.png);
        }

        #logo-inner-4 {
            top: 24vh;
            left: 0vh;
            background-image: url(/images/Icons/Tile-U2-shadow.png);
        }

        #logo-inner-5 {
            top: 24vh;
            left: 12vh;
            background-image: url(/images/Icons/Tile-X-shadow.png);
        }
